<template>
  <div class="edittags">
    <div class="head">
      <i class="iconfont icon-xiangzuojiantou" @click="$router.back()"></i>
      <p >编辑标签</p>
    </div>
    <label>
      标签名<input v-model="tagName" @blur="updataTagName">
    </label>
    <button @click="deleteTag">删除标签</button>
  </div>
</template>

<script>

export default {
  name: 'EditTags',
  data () {
    return {
      id: (parseInt(this.$route.params.id)),
      tagName: ''
    }
  },
  methods: {
    updataTagName () {
      this.$store.commit('updataTag', { id: this.id, name: this.tagName })
    },
    deleteTag () {
      this.$store.commit('deleteTag', this.id)
      this.$router.back()
    }
  },
  created () {
    const tags = this.$store.state.tags
    for (let i = 0; i < tags.length; i++) {
      if (tags[i].id === this.id) {
        this.tagName = tags[i].tagName
      }
    }
  }
}
</script>

<style lang="less" scoped>
    .edittags{
      background-color: #f5f5f5;
      position: relative;
      min-height: 100vh;
    }
    .head{
      display: flex;
      align-items: center;
      padding:  (15/@viewport);
      background-color: #fff;
      p{
        margin-left: (125/@viewport);
      }
    }
    label{
      margin-top: (10/@viewport);
      background-color: #fff;
      display: block;
      padding: (15/@viewport);
    }
    input{
      margin-left: (10/@viewport);
      outline: none;
      border: 0;
      height: 100%;
      font-size: (16/@viewport);
      color: #8c8c8c;
    }
    button{
      padding: (10/@viewport) (10/@viewport);
      border: 0;
      background-color: #8c8c8c;
      color: #ffecec;
      border-radius: (5/@viewport);
      position: absolute;
      left: 50%;
      bottom: 30%;
      transform: translate(-50%,-50%);
    }
</style>
