<template>
  <Layout class="labels">
    <template v-slot:layout-slot>
      <ul @click="editTag">
        <li v-for="item in tags" :key="item.id"  :data-id="item.id">
          <p  :data-id="item.id">{{item.tagName}}</p>
          <i class="iconfont icon-jinrujiantou" :data-id="item.id"></i>
        </li>
      </ul>
      <button @click="newTag">新建标签</button>
    </template>
  </Layout>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'Labels',
  computed: mapState(['tags']),
  methods: {
    newTag () {
      this.$router.push('/newtag')
    },
    editTag (e) {
      this.$router.push(`/edittags/${e.target.dataset.id}`)
    }
  }

}
</script>

<style lang="less" scoped>
  .labels{
    background-color: #f5f5f5;
    color: #000000;
    position: relative;
    button{
      padding: (10/@viewport) (10/@viewport);
      border: 0;
      background-color: #8c8c8c;
      color: #ffecec;
      border-radius: (5/@viewport);
      position: absolute;
      left: 50%;
      top: 30%;
      transform: translate(-50%,-50%);
    }
  }
  li{
      width: 100%;
      padding:(5/@viewport) (15/@viewport) ;
      display: flex;
      justify-content: space-between;
     background-color: #fff;
    border-bottom: 1px solid #e0e0e0;
    i{
      font-size: (25/@viewport);
      text-align:center;
      &::before{
        display: block;
        text-align: center;
      }
    }
    }
</style>
