<template>
  <div class="tagsContainer" >
    <ul @click="selectTag($event)" ref="ulRef">
      <li v-for="item in tags" :key="item.id" :tagid="item.id" >{{item.tagName}}</li>
    </ul>
    <p @click="newTag">新增标签</p>
  </div>
</template>

<script>

export default {
  name: 'Tags',
  data () {
    return {
      tags: this.$store.state.tags
    }
  },
  computed: {
    selectTags () { return this.$store.state.obj.selectTags }
  },
  methods: {
    selectTag (e) {
      if (e.target === this.$refs.ulRef) return
      e.target.classList.toggle('active')
      this.$store.commit('updateSelectTags', e.target.getAttribute('tagid'))
    },
    newTag () {
      this.$router.push('/newtag')
    }
  },
  mounted () {
    const li = this.$refs.ulRef.children
    for (let i = 0; i < li.length; i++) {
      if (this.selectTags.indexOf(li[i].getAttribute('tagid')) !== -1) {
        li[i].className = 'active'
      }
    }
  }
}
</script>

<style lang="less" scoped>
.tagsContainer{
  background-color: #fff;
  padding-bottom: (10/@viewport);
  flex:1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: (15/@viewport);
  p{
    color:#999;
    font-size: (14/@viewport);
    border-bottom: 1px solid #222222;
    width: (70/@viewport);
    text-align: center;
    margin-top: (5/@viewport);
  }
  ul{
    display: flex;
    font-size: (12/@viewport);
    flex-wrap: wrap;
    padding: (7/@viewport);
    justify-content: space-between;
    li{
      color:#636363;
      margin-top: (10/@viewport);
      line-height: 2;
      padding: 0 (18/@viewport);
      border-radius: (15/@viewport);
      background-color: #d9d9d9;
      &.active{
       background-color: #f5f5f5;
        transform: scale(1.2);
        box-shadow: 0 0 1px rgba(0,0,0,0.3);
      }
    }
  }
}
</style>
