<template>
<div class="billTypeContainer" @click="switchType">
  <div :class="billName==='支出'?'active':''" ref="outlayRef"  >支出</div>
  <div ref="earningRef" :class="billName==='收入'?'active':''" >收入</div>
</div>
</template>

<script>
export default {
  name: 'BillType',
  data () {
    return {
      billName: this.$store.state.obj.billType
    }
  },
  methods: {
    switchType (e) {
      this.$refs.outlayRef.className = ''
      this.$refs.earningRef.className = ''
      e.target.className = 'active'
      this.$store.commit('updateBillType', e.target.innerHTML)
    }
  }

}
</script>

<style lang="less" scoped>
    .billTypeContainer{
      background-color: #c4c4c4;
      font-size: (18/@viewport);
      display: flex;
      div{
        padding: (15/@viewport);
        width: 50%;
        text-align: center;
        position: relative;
      }
      .active:before{
        content:"";
        display: block;
        background-color: #969696;
        width: 100%;
        height: 3px;
        position: absolute;
        bottom: 0;
        left: 0;
      }

    }
</style>
