const util = {
  paddingZero (num) {
    return num >= 10 ? num : '0' + num
  },
  getDate (string) {
    const data = string || new Date()
    return `${data.getFullYear()}-${this.paddingZero(data.getMonth() + 1)}-${this.paddingZero(data.getDate())} ${this.paddingZero(data.getHours())}:${this.paddingZero(data.getMinutes())}:${this.paddingZero(data.getSeconds())}`
  },
  init () {
    return localStorage.getItem('小文记账')
      ? JSON.parse(localStorage.getItem('小文记账'))
      : {
          tags: [
            { id: 1, tagName: '衣' },
            { id: 2, tagName: '食' },
            { id: 3, tagName: '住' },
            { id: 4, tagName: '行' }
          ],
          tagNextId: 5,
          costRecords: []
        }
  },
  getWeek (sss) {
    const date = '1970-1-5 00:00:00'
    const week = 1000 * 60 * 60 * 24 * 7
    return Math.ceil((Date.parse(sss) - Date.parse(date)) / week)
  },
  getStartDate (num) {
    const week = 1000 * 60 * 60 * 24 * 7
    const date = '1970-1-5 00:00:00'
    const startDate = new Date((num - 1) * week + Date.parse(date))
    const endDate = new Date(num * week + Date.parse(date) - 1000 * 60 * 60 * 24)
    return (util.getDate(startDate).toString().substring(0, 10) + '~' + util.getDate(endDate).toString().substring(0, 10))
  }

}
export default util
