<template>
  <Layout class="statistics">
    <template v-slot:layout-slot>
      <div class="head">统计</div>
      <div class="billtype" @click="switchType">
        <div :class="billtype==='支出'?'active':''">支出</div>
        <div :class="billtype==='收入'?'active':''">收入</div>
      </div>
      <div class="lable" @click="switchLableType">
        <p :class="lableType==='按天'?'active':''">按天</p>
        <p :class="lableType==='按周'?'active':''">按周</p>
        <p :class="lableType==='按月'?'active':''">按月</p>
        </div>
        <div v-for="xxx  in costRecords"  :key="xxx.time">
          <div class="time">
            <p>{{xxx.time}}</p>
            <p>{{'¥'+xxx.sum}}</p>
          </div>
          <div class="particular" v-for="item,index in xxx.jilu" :key="index">
            <p>
              <span v-for="i in item.selectTags" :key="i" class="tag"> {{tags[i]+' '}}</span>
            </p>
            <p class="comment">{{item.comment}}</p>
            <p class="money">{{'¥'+item.money}}</p>
          </div>
        </div>
    </template>
  </Layout>
</template>

<script>
import util from '@/util/util'

export default {
  data () {
    return {
      billtype: '支出',
      lableType: '按天'
    }
  },
  computed: {
    costRecords () {
      const check = this.$store.state.costRecords.filter(item => item.billType === this.billtype)
      let time = []
      const arr = []
      if (this.lableType === '按天' || this.lableType === '按月') {
        for (let i = 0; i < check.length; i++) {
          time.push(check[i].time.substr(0, this.lableType === '按月' ? 7 : 10))
        }
        time = Array.from(new Set(time)).reverse()
        for (let i = 0; i < time.length; i++) {
          const obj = { time: time[i] }
          obj.jilu = check.filter(item => item.time.indexOf(time[i]) !== -1)
          obj.sum = 0
          for (let i = 0; i < obj.jilu.length; i++) {
            obj.sum += parseInt(obj.jilu[i].money)
          }
          arr.push(obj)
        }
      }
      if (this.lableType === '按周') {
        for (let i = 0; i < check.length; i++) {
          time.push(util.getWeek(check[i].time))
        }
        time = Array.from(new Set(time)).reverse()
        console.log(time)
        for (let i = 0; i < time.length; i++) {
          const obj = { time: util.getStartDate(time[i]) }
          obj.jilu = check.filter(item => util.getWeek(item.time) === time[i])
          obj.sum = 0
          for (let i = 0; i < obj.jilu.length; i++) {
            obj.sum += parseInt(obj.jilu[i].money)
          }
          arr.push(obj)
        }
      }
      return arr
    },
    tags () {
      const tagObj = this.$store.state.tags
      const obj = {}
      for (let i = 0; i < tagObj.length; i++) {
        obj[tagObj[i].id] = tagObj[i].tagName
      }
      return obj
    }
  },
  name: 'Statistics',
  methods: {
    switchType (e) {
      this.billtype = e.target.innerHTML
    },
    switchLableType (e) {
      this.lableType = e.target.innerHTML
    }
  }
}
</script>

<style lang="less" scoped>
  .statistics{
    background-color: #f0f0f0;
    .head{
      padding:(15/@viewport);
      font-weight: 550;
      text-align: center;
    }
    .billtype{
      background-color: #fff;
      display: flex;
      font-weight: 450;
      div{
        padding: (15/@viewport);
        width: 50%;
        text-align: center;
        font-size: (18/@viewport);
        letter-spacing:(5/@viewport);
        &.active{
          background-color:  #8c8c8c;
        }
      }
    }
    .lable{
      background-color: #8c8c8c;
      display: flex;
      p{
        padding:  (10/@viewport) 0;
        flex: 1;
        text-align: center;
        position: relative;
        &.active:before{
          content: "";
          display: block;
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: (3/@viewport);
          background-color: #222222;
        }
      }
    }
    .time{
      padding: (10/@viewport) (5/@viewport);
      display: flex;
      justify-content: space-between;
    }
    .particular{
      padding: (0/@viewport) (5/@viewport);
      display: flex;
      justify-content: space-between;
      background-color: #fff;
      position: relative;
      border-bottom: 1px solid silver;
      align-items: center;
      .money{
        line-height: 3;
      }
      .tag{
        color:#636363;
        border-radius: (15/@viewport);
        padding: 0 (10/@viewport);
        margin-left: (5/@viewport);
        background-color: #d9d9d9;
      }
      .comment{
        color:#8c8c8c;
      }
    }
  }
</style>
