<template>
  <div class="numberPanelContainer">
    <p>{{obj.money}}</p>
    <ul @click="input">
      <li>1</li>
      <li>2</li>
      <li>3</li>
      <li>删除</li>
      <li>4</li>
      <li>5</li>
      <li>6</li>
      <li>清空</li>
      <li>7</li>
      <li>8</li>
      <li>9</li>
      <li>ok</li>
      <li>0</li>
      <li>.</li>
    </ul>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      content: this.$store.state.obj.money
    }
  },
  computed: mapState(['obj']),

  name: 'NumberPanel',
  methods: {
    input (e) {
      const content = e.target.innerHTML
      if (content === '清空') {
        this.obj.money = '0'
      } else if (content === '删除') {
        this.obj.money = this.obj.money.length > 1 ? this.obj.money.substring(0, this.obj.money.length - 1) : '0'
      } else if (content === '.') {
        if (this.obj.money.indexOf('.') !== -1) return
        this.obj.money += '.'
      } else if (content === 'ok') {
        if (this.obj.money !== '0') {
          this.$store.commit('addCostRecords')
          this.$router.go(0)
        }
      } else {
        if (this.obj.money === '0' && this.obj.money.length === 1) {
          this.obj.money = content
        } else {
          this.obj.money += content
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
 .numberPanelContainer{
    p{
      font-size: (30/@viewport);
      padding: (15/@viewport) (5/@viewport);
      background-color: #fff;
      text-align: right;
    }
   ul{
     width: 100%;
    background-color: #fff;
     box-shadow: 0px -2px 2px rgba(0,0,0,0.1);
     display: flex;
     flex-wrap: wrap;
     position: relative;
     li{
        width: 25%;
       font-size: (20/@viewport);
       text-align: center;
       line-height: 3;
       &:nth-child(1){
        background-color: #f2f2f2;
       }
       &:nth-child(2),&:nth-child(5){
         background-color: #e0e0e0;
       }
       &:nth-child(3),&:nth-child(6),&:nth-child(9){
         background-color: #d3d3d3;
       }
       &:nth-child(4),&:nth-child(7),&:nth-child(10),&:nth-child(13){
         background-color: #b4b4b4;
       }
       &:nth-child(8),&:nth-child(11){
         background-color: #a0a0a0;
       }
       &:nth-child(12){
         position: absolute;
         bottom: 0;
         right: 0;
         background-color: #8c8c8c;
         line-height: 6;
       }
       &:nth-child(13){
         width: 50%;
         background-color: #9b9b9b;
       }
       &:nth-child(14){
         background-color: #969696;
       }
      }
   }
 }
</style>
