<template>
  <div class="edittags">
    <div class="head">
      <i class="iconfont icon-xiangzuojiantou" @click="$router.back()"></i>
      <p>新建标签</p>
    </div>
    <label>
      标签名<input v-model="tagName">
    </label>
    <button @click="saveTag" >保存标签</button>
  </div>
</template>

<script>
export default {
  name: 'NewTags',
  data () {
    return {
      tagName: ''
    }
  },
  methods: {
    saveTag () {
      if (this.tagName.trim() !== '') {
        this.$store.commit('createTag', this.tagName.trim())
        this.tagName = ''
        this.$router.back()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.edittags{
  background-color: #f5f5f5;
  position: relative;
  min-height: 100vh;
}
.head{
  display: flex;
  align-items: center;
  padding:  (15/@viewport);
  background-color: #fff;
  p{
    margin-left: (125/@viewport);
  }
}
label{
  margin-top: (10/@viewport);
  background-color: #fff;
  display: block;
  padding: (15/@viewport);
}
input{
  margin-left: (10/@viewport);
  outline: none;
  border: 0;
  height: 100%;
  font-size: (16/@viewport);
  color: #8c8c8c;
}
button{
  padding: (10/@viewport) (10/@viewport);
  border: 0;
  background-color: #8c8c8c;
  color: #ffecec;
  border-radius: (5/@viewport);
  position: absolute;
  left: 50%;
  bottom: 30%;
  transform: translate(-50%,-50%);
}
</style>
