import { createStore } from 'vuex'
import util from '@/util/util'

export default createStore({
  state: {
    tags: util.init().tags,
    tagNextId: util.init().tagNextId,
    costRecords: util.init().costRecords,
    obj: {
      selectTags: [],
      comment: '',
      billType: '支出',
      money: '0',
      time: undefined
    }
  },
  getters: {
  },
  mutations: {

    createTag (state, tagName) {
      for (let i = 0; i < state.tags.length; i++) {
        if (state.tags[i].tagName === tagName) { return }
      }
      state.tags.push({
        id: state.tagNextId,
        tagName: tagName
      })
      state.tagNextId++
      this.commit('save')
    },
    deleteTag (state, id) {
      state.tags = state.tags.filter(item => item.id !== id)
      this.commit('save')
    },
    updataTag (state, obj) {
      for (let i = 0; i < this.state.tags.length; i++) {
        if (this.state.tags[i].id === obj.id && this.state.tags[i].tagName !== obj.name) {
          this.state.tags[i].tagName = obj.name
          this.commit('save')
        }
      }
    },
    save () {
      const info = {
        tags: this.state.tags,
        tagNextId: this.state.tagNextId,
        costRecords: this.state.costRecords
      }
      localStorage.setItem('小文记账', JSON.stringify(info))
      this.commit('resetObj')
    },
    resetObj () {
      this.state.obj = {
        selectTags: [],
        comment: '',
        billType: '支出',
        money: '0',
        time: undefined
      }
    },
    addCostRecords (state) {
      state.obj.time = util.getDate()
      state.costRecords.push(state.obj)
      this.commit('save')
    },
    updateSelectTags (state, id) {
      const index = state.obj.selectTags.indexOf(id)
      if (index !== -1) {
        state.obj.selectTags.splice(index, 1)
      } else {
        state.obj.selectTags.push(id)
      }
    },
    updateBillType (state, type) {
      if (state.obj.billType !== type) {
        state.obj.billType = type
      }
    },
    updateMoney (state, money) {
      state.obj.money = money
    }
  },
  actions: {
  },
  modules: {
  }
})
